import { useContext, useMemo, createContext, ComponentProps } from 'react';
import useThunkReducer, { ThunkDispatch } from 'react-hook-thunk-reducer';

import fetchCollection, { GlassesColourFilter, GlassesFrameFilter, GlassesItem } from 'src/api/collection';

import { SimpleAction } from './common';

const DEFAULT_COLLECTION_ID = 'spectacles-women';
const ITEMS_PER_PAGE = 12;
const FIRST_PAGE = 1;

// store
export interface CollectionStore {
  glasses: Array<GlassesItem>;
  collectionId: string;
  page: number;
  total: number;
  colourFilter: GlassesColourFilter;
  frameFilter: GlassesFrameFilter;
  loading: boolean;
  error: string | null;
}

interface CollectionContextData {
  state: CollectionStore;
  dispatch: ThunkDispatch<CollectionStore, SimpleAction> | null;
}

export const initialState: CollectionStore = {
  glasses: [],
  collectionId: DEFAULT_COLLECTION_ID,
  page: FIRST_PAGE,
  total: 0,
  colourFilter: {},
  frameFilter: {},
  loading: false,
  error: null
};

// actions
export const FETCH_COLLECTION_START = 'FETCH_COLLECTION_START';
export const FETCH_COLLECTION_COMPLETE = 'FETCH_COLLECTION_COMPLETE';
export const FETCH_COLLECTION_ERROR = 'FETCH_COLLECTION_ERROR';
export const SET_COLLECTION_ID = 'SET_COLLECTION_ID';
export const SET_PAGE = 'SET_PAGE';
export const SET_FILTERS = 'SET_FILTERS';

export const fetchStart = (invalidate: boolean) => ({
  type: FETCH_COLLECTION_START,
  payload: {
    invalidate
  }
});

export const fetchComplete = (glasses: Array<GlassesItem>, total: number, invalidate: boolean) => ({
  type: FETCH_COLLECTION_COMPLETE,
  payload: {
    glasses,
    total,
    invalidate
  }
});

export const fetchError = (error: string | null = null) => ({
  type: FETCH_COLLECTION_ERROR,
  payload: {
    error
  }
});

export const setCollectionId = (collectionId: string) => ({
  type: SET_COLLECTION_ID,
  payload: {
    collectionId
  }
});

export const setPage = (page: number) => ({
  type: SET_PAGE,
  payload: {
    page
  }
});

export const setFilters = (colourFilter: GlassesColourFilter, frameFilter: GlassesFrameFilter) => ({
  type: SET_FILTERS,
  payload: {
    colourFilter,
    frameFilter
  }
});

// thunks
export const fetchGlasses = (invalidate: boolean) => async (
  dispatch: ThunkDispatch<CollectionStore, SimpleAction>,
  getState: () => CollectionStore
) => {
  const { collectionId, page, colourFilter, frameFilter } = getState();
  try {
    dispatch(fetchStart(invalidate));
    const result = await fetchCollection(collectionId, ITEMS_PER_PAGE, page, colourFilter, frameFilter);
    dispatch(fetchComplete(result.glasses, result.meta.totalCount, invalidate));
  } catch (e) {
    dispatch(fetchError(e as string));
  }
};

export const changeCollectionId = (collectionId: string) => (
  dispatch: ThunkDispatch<CollectionStore, SimpleAction>
) => {
  dispatch(setCollectionId(collectionId));
  // reset pagination when different collection is loaded
  dispatch(setPage(FIRST_PAGE));
  dispatch(fetchGlasses(true));
};

export const fetchNextPage = () => (
  dispatch: ThunkDispatch<CollectionStore, SimpleAction>,
  getState: () => CollectionStore
) => {
  const { page, total, loading } = getState();
  if (!loading && page <= Math.floor((total - 1) / ITEMS_PER_PAGE)) {
    dispatch(setPage(page + 1));
    dispatch(fetchGlasses(false));
  }
};

export const updateFilters = (colourFilter: GlassesColourFilter, frameFilter: GlassesFrameFilter) => (
  dispatch: ThunkDispatch<CollectionStore, SimpleAction>
) => {
  dispatch(setFilters(colourFilter, frameFilter));
  // reset pagination when filter is changed
  dispatch(setPage(FIRST_PAGE));
  dispatch(fetchGlasses(true));
};

const reducer = (state: CollectionStore, action: SimpleAction): CollectionStore => {
  const { type } = action;
  switch (type) {
    case FETCH_COLLECTION_START: {
      const { invalidate } = (action as ReturnType<typeof fetchStart>).payload;
      if (invalidate) {
        return { ...state, glasses: [], loading: true };
      }
      return { ...state, loading: true };
    }
    case FETCH_COLLECTION_COMPLETE: {
      const { glasses, total, invalidate } = (action as ReturnType<typeof fetchComplete>).payload;
      if (invalidate) {
        return { ...state, loading: false, glasses, total };
      }
      return {
        ...state,
        loading: false,
        glasses: [...state.glasses, ...glasses],
        total
      };
    }
    case FETCH_COLLECTION_ERROR: {
      const { error } = (action as ReturnType<typeof fetchError>).payload;
      return { ...state, loading: false, error };
    }
    case SET_COLLECTION_ID: {
      const { collectionId } = (action as ReturnType<typeof setCollectionId>).payload;
      return { ...state, collectionId };
    }
    case SET_PAGE: {
      const { page } = (action as ReturnType<typeof setPage>).payload;
      return { ...state, page };
    }
    case SET_FILTERS: {
      const { colourFilter, frameFilter } = (action as ReturnType<typeof setFilters>).payload;
      return { ...state, colourFilter, frameFilter };
    }
    default: {
      return { ...state };
    }
  }
};

// Context
const CollectionContext = createContext<CollectionContextData>({ state: initialState, dispatch: null });

export const CollectionProvider = (props: ComponentProps<any>) => {
  const [state, dispatch] = useThunkReducer(reducer, initialState);
  const { Provider } = CollectionContext;

  return <Provider value={useMemo(() => ({ state, dispatch }), [state, dispatch])} {...props} />;
};

export const useCollectionContext = () => useContext(CollectionContext);
