import { useContext, useMemo, createContext, useEffect, ComponentProps } from 'react';
import useThunkReducer, { ThunkDispatch } from 'react-hook-thunk-reducer';
import { createSelector } from 'reselect';

import fetchCollectionCategories, { CollectionCategory } from 'src/api/collectionCategories';

import { SimpleAction } from './common';

// store
export interface CollectionCategoriesStore {
  categories: Array<CollectionCategory>;
  loading: boolean;
  loaded: boolean;
  error: string | null;
}

interface CollectionCategoriesContextData {
  state: CollectionCategoriesStore;
  dispatch: ThunkDispatch<CollectionCategoriesStore, SimpleAction> | null;
}

interface CollectionSubcategory {
  name: string;
  configurationName: string;
}

interface CategorizedCollection extends CollectionCategory {
  subcategories: Array<CollectionSubcategory>;
}

interface CategorizedCollections {
  [key: string]: CategorizedCollection;
}

export const initialState: CollectionCategoriesStore = {
  categories: [],
  loading: false,
  loaded: false,
  error: null
};

// selectors
const rootSelector = (state: CollectionCategoriesStore) => state;

export const getCategories = createSelector(rootSelector, ({ categories }) => categories);

// TODO: vgrynch: inform API team about below cringe to improve data schema and separate category/subcategory structurally
// UI code should never make transformations like this one, especially splitting keys and titles
export const getCategorizedCollections = createSelector(
  getCategories,
  (categories): Array<CategorizedCollection> => {
    return Object.values(
      categories.reduce((result: CategorizedCollections, category: CollectionCategory): CategorizedCollections => {
        const [categoryName, subcategoryName] = category.name.split(' ');
        const [categoryConfig, subcategoryConfig] = category.configurationName.split('-');
        const newCategory: CategorizedCollection = result[categoryConfig] || {
          ...category,
          name: categoryName,
          configurationName: categoryConfig,
          subcategories: []
        };
        newCategory.subcategories.push({
          name: subcategoryName,
          configurationName: subcategoryConfig
        });

        return {
          ...result,
          [categoryConfig]: newCategory
        };
      }, {} as CategorizedCollections)
    );
  }
);

// actions
export const FETCH_COLLECTION_CATEGORIES_START = 'FETCH_COLLECTION_CATEGORIES_START';
export const FETCH_COLLECTION_CATEGORIES_COMPLETE = 'FETCH_COLLECTION_CATEGORIES_COMPLETE';
export const FETCH_COLLECTION_CATEGORIES_ERROR = 'FETCH_COLLECTION_CATEGORIES_COMPLETE';

export const fetchStart = () => ({ type: FETCH_COLLECTION_CATEGORIES_START });

export const fetchComplete = (categories: Array<CollectionCategory>) => ({
  type: FETCH_COLLECTION_CATEGORIES_COMPLETE,
  payload: {
    categories
  }
});

export const fetchError = (error: string | null = null) => ({
  type: FETCH_COLLECTION_CATEGORIES_ERROR,
  payload: {
    error
  }
});

// thunks
export const fetchCategories = () => async (dispatch: ThunkDispatch<CollectionCategoriesStore, SimpleAction>) => {
  try {
    dispatch(fetchStart());
    const result = await fetchCollectionCategories();
    // TODO: vgrynch: results should be already returned properly sorted by API
    result.collections.sort((a, b) => a.id - b.id);
    dispatch(fetchComplete(result.collections));
  } catch (e) {
    dispatch(fetchError(e as string));
  }
};

const reducer = (state: CollectionCategoriesStore, action: SimpleAction) => {
  const { type } = action;
  switch (type) {
    case FETCH_COLLECTION_CATEGORIES_START: {
      return { ...state, loading: true, loaded: false };
    }
    case FETCH_COLLECTION_CATEGORIES_COMPLETE: {
      const { categories } = (action as ReturnType<typeof fetchComplete>).payload;
      return { ...state, loading: false, loaded: true, categories };
    }
    case FETCH_COLLECTION_CATEGORIES_ERROR: {
      const { error } = (action as ReturnType<typeof fetchError>).payload;
      return { ...state, loading: false, laded: false, error };
    }
    default: {
      return { ...state };
    }
  }
};

// Context
const CollectionCategoriesContext = createContext<CollectionCategoriesContextData>({
  state: initialState,
  dispatch: null
});

export const CollectionCategoriesProvider = (props: ComponentProps<any>) => {
  const [state, dispatch] = useThunkReducer(reducer, initialState);

  // TODO: vgrynch: move this out into App to avoid double-call
  useEffect(() => {
    dispatch(fetchCategories());
  }, []);

  const { Provider } = CollectionCategoriesContext;
  return <Provider value={useMemo(() => ({ state, dispatch }), [state, dispatch])} {...props} />;
};

export const useCollectionCategoriesContext = () => useContext(CollectionCategoriesContext);
