import { useEffect, useRef, useState } from 'react';
import cn from 'classnames';

import { useCollectionContext, fetchNextPage } from 'src/modules/store/collection';
import { GlassesItem, GlassesVariant } from 'src/api/collection';

import styles from './Gallery.module.scss';

const Gallery = () => {
  const [observer, setObserver] = useState<IntersectionObserver | null>(null);
  const {
    state: { glasses, loading },
    dispatch: collectionDispatch
  } = useCollectionContext();
  const galleryRef = useRef<HTMLDivElement>(null);

  const pickDefaultVariant = (glassesItem: GlassesItem): GlassesVariant =>
    glassesItem.glassVariants.find((variant) => variant.defaultGlassVariant) || glassesItem.glassVariants[0];

  useEffect(() => {
    const handleNextPage = (entries: IntersectionObserverEntry[], currentObserver: IntersectionObserver) => {
      if (collectionDispatch && entries.every((entry) => entry.isIntersecting)) {
        entries.forEach((entry) => currentObserver.unobserve(entry.target));

        collectionDispatch(fetchNextPage());
      }
    };

    if (galleryRef.current && galleryRef.current.lastChild) {
      let currentObserver = observer;
      if (!currentObserver) {
        const options = {
          root: document,
          rootMargin: '0px',
          threshold: 0.01
        };
        currentObserver = new IntersectionObserver(handleNextPage, options);
        setObserver(currentObserver);
      }
      currentObserver.observe(galleryRef.current.lastChild as HTMLElement);
    }
  }, [loading, galleryRef]);

  let numColumns = 2;
  if (window && window.matchMedia) {
    const dq = window.matchMedia('(min-width: 1366px)');
    const mq = window.matchMedia('(max-width: 719.9px)');
    if (mq.matches) {
      numColumns = 1;
    } else if (dq.matches) {
      numColumns = 3;
    }
  }

  return (
    <>
      {!!glasses.length && (
        <div className={styles.container} ref={galleryRef}>
          {glasses.map((glassesItem, index) => {
            const variant = pickDefaultVariant(glassesItem);
            return (
              <div
                className={cn(styles.cell, {
                  [styles.cellLast]: index + ((glasses.length - 1) % numColumns) + 1 >= glasses.length
                })}
                key={glassesItem.id}
              >
                {/* By using the aspect ratio placeholder (that works as background as well),
                we make sure the cell will shrink properly before the true image is loaded. This also makes
                entire grid resize really much faster with no visual "jumping" */}
                <div className={styles.thumbnailAspect} />
                <div className={styles.thumbnail}>
                  <img src={variant.media[0].url} alt={`${variant.frameVariant.name}`} />
                  <div className={styles.thumbnailTitle}>{glassesItem.name}</div>
                </div>
              </div>
            );
          })}
        </div>
      )}
      <div className={cn(styles.loading, { [styles.loadingShow]: loading })}>Loading...</div>
    </>
  );
};

export default Gallery;
