/* eslint-disable jsx-a11y/tabindex-no-positive */
import { useEffect, useState, useRef, UIEvent } from 'react';
import cn from 'classnames';

import SideMenu from './SideMenu';
import { ReactComponent as MenuCloseIcon } from './menuClose.svg';

import styles from './Menu.module.scss';
import handleButtonKeys from '../../../utils/handleButtonKeys';

// because user can sometimes move mouse out of the edges and between elements, we should
// avoid jumping (quick trying close menu then quick re-open). For that, we start closing
// it by a small timeout
const SIDE_MENU_CLOSE_TIMEOUT = 100;

const Menu = () => {
  const [isClickOpened, setIsClickOpened] = useState(false);
  const [isMenuOpened, setIsMenuOpened] = useState(false);
  const [isMenuFocused, setIsMenuFocused] = useState(false);
  const [isMenuHovered, setIsMenuHovered] = useState(false);
  const [isClosing, setIsClosing] = useState(false);

  // below is needed to re-assure everything is already created in DOM
  // before we try to show menu, otherwise it may just pop up display instead of slide out
  const menuRef = useRef<HTMLDivElement>(null);
  const hideTimer = useRef<number>();

  const handleTopButtonClick = (event: UIEvent) => {
    event.preventDefault();
    event.stopPropagation();

    const mq = window.matchMedia('(max-width: 719.9px)');
    const isMobile = mq.matches;
    if (isMobile) {
      document.activeElement instanceof HTMLElement && document.activeElement.blur();
      setIsClosing(() => true);
      setIsClickOpened((currentIsClickOpened) => !currentIsClickOpened);
      setIsMenuOpened(() => false);
    }
  };

  const handleSideMenuOpen = () => setIsMenuHovered(() => true);

  const handleSideMenuOut = () => setIsMenuHovered(() => false);

  const handleSideMenuFocus = () => setIsMenuFocused(() => true);

  const handleSideMenuBlur = () => setIsMenuFocused(() => false);

  const handleMenuNavigation = () => {
    setIsClickOpened(() => false);
    setIsMenuOpened(() => false);
    setIsClosing(() => true);
  };

  const handleForceClose = () => {
    if (isMenuOpened || isClickOpened) {
      setIsClosing(() => true);
    }
    setIsClickOpened(() => false);
    setIsMenuOpened(() => false);
  };

  useEffect(() => {
    if (menuRef.current) {
      if (isClosing) {
        setTimeout(() => setIsClosing(() => false), 200);
      } else if (isMenuHovered || isMenuFocused) {
        setIsMenuOpened(() => true);
        if (hideTimer.current) {
          clearTimeout(hideTimer.current);
          hideTimer.current = 0;
        }
      } else if (!hideTimer.current) {
        if (window) {
          hideTimer.current = window.setTimeout(() => setIsMenuOpened(() => false), SIDE_MENU_CLOSE_TIMEOUT);
        } else {
          setIsMenuOpened(() => false);
        }
      }
    }
  }, [isMenuHovered, isMenuFocused, menuRef]);

  return (
    <>
      <div className={styles.container} role="menubar">
        {/* TODO: vgrynch: handle click only for responsive!!! */}
        <div
          className={styles.topButton}
          role="menuitem"
          tabIndex={200}
          aria-label={isClickOpened ? 'Close side menu' : 'Open side menu'}
          aria-haspopup="menu"
          aria-expanded={isClickOpened || isMenuOpened}
          onMouseDown={(event) => event.stopPropagation()}
          onClick={handleTopButtonClick}
          onKeyDown={(event) => handleButtonKeys(event, () => handleTopButtonClick(event))}
          onMouseOver={handleSideMenuOpen}
          onMouseOut={handleSideMenuOut}
          onFocus={handleSideMenuFocus}
          onBlur={handleSideMenuBlur}
        >
          {isClickOpened ? (
            <MenuCloseIcon />
          ) : (
            <span className={cn(styles.topButtonSpan, { [styles.topButtonSpanHover]: isMenuOpened })}>Menu</span>
          )}
        </div>
        <div className={styles.home}>
          <a href={process.env.PUBLIC_URL} role="menuitem" aria-label="Home page" title="Home page">
            {/* TODO: vgrynch: this probably should be in common assets folder or converted to SVG */}
            <img src={`${process.env.PUBLIC_URL}/bloobloom.ico`} alt="Bloobloom icon" width={20} height={20} />
          </a>
        </div>
        <div className={styles.placeholder} role="separator" />
      </div>
      <div className={styles.spacer} />
      <div
        ref={menuRef}
        role="menu"
        className={cn(styles.sideContainer, {
          [styles.sideOpen]: isClickOpened || isMenuOpened,
          [styles.sideClose]: !(isClickOpened || isMenuOpened)
        })}
        tabIndex={-1}
        onMouseOver={handleSideMenuOpen}
        onMouseOut={handleSideMenuOut}
        onFocus={handleSideMenuFocus}
        onBlur={handleSideMenuBlur}
      >
        <SideMenu onMenuClicked={handleMenuNavigation} forceClose={isClosing} onForceClose={handleForceClose} />
      </div>
    </>
  );
};

export default Menu;
