/* eslint-disable jsx-a11y/tabindex-no-positive */
import { UIEvent } from 'react';

import { GlassesColourTypes, GlassesFrameTypes } from 'src/api/collection';
import { useCollectionContext, updateFilters } from 'src/modules/store/collection';
import handleButtonKeys from 'src/utils/handleButtonKeys';

import { ReactComponent as RemoveFilter } from './removeFilter.svg';
import styles from './Filters.module.scss';

const CurrentFilter = () => {
  const {
    state: { colourFilter, frameFilter, total },
    dispatch: collectionDispatch
  } = useCollectionContext();

  const removeColorFilter = (event: UIEvent, colour: GlassesColourTypes) => {
    event.preventDefault();
    event.stopPropagation();

    const newFilter = {
      ...colourFilter
    };
    delete newFilter[colour];
    collectionDispatch && collectionDispatch(updateFilters(newFilter, frameFilter));
  };

  const removeFrameFilter = (event: UIEvent, frame: GlassesFrameTypes) => {
    event.preventDefault();
    event.stopPropagation();

    const newFilter = {
      ...frameFilter
    };
    delete newFilter[frame];
    collectionDispatch && collectionDispatch(updateFilters(colourFilter, newFilter));
  };

  const clearFilters = (event: UIEvent) => {
    event.preventDefault();
    event.stopPropagation();

    collectionDispatch && collectionDispatch(updateFilters({}, {}));
  };

  return (
    <div className={styles.current} role="search" aria-label="Currently used filter displaying and managing">
      <div className={styles.box}>
        {(Object.keys(colourFilter) as GlassesColourTypes[]).map((colourType) => (
          <div key={`colour${colourType}`} className={styles.currentItem}>
            <span>{colourType}</span>
            <div
              aria-label={`Click to remove the "${colourType}" from the filter`}
              role="button"
              tabIndex={600}
              onClick={(event) => removeColorFilter(event, colourType)}
              onKeyDown={(event) => handleButtonKeys(event, () => removeColorFilter(event, colourType))}
              className={styles.removeButton}
            >
              <RemoveFilter />
            </div>
          </div>
        ))}
        {(Object.keys(frameFilter) as GlassesFrameTypes[]).map((frameType) => (
          <div key={`frame${frameType}`} className={styles.currentItem}>
            <span>{frameType}</span>
            <div
              aria-label={`Click to remove the "${frameType}" from the filter`}
              role="button"
              tabIndex={600}
              onClick={(event) => removeFrameFilter(event, frameType)}
              onKeyDown={(event) => handleButtonKeys(event, () => removeFrameFilter(event, frameType))}
              className={styles.removeButton}
            >
              <RemoveFilter />
            </div>
          </div>
        ))}
      </div>
      <div className={styles.total}>{total} results found</div>
      <div className={styles.clear}>
        <div
          className={styles.clearLink}
          role="button"
          tabIndex={700}
          onClick={clearFilters}
          onKeyDown={(event) => handleButtonKeys(event, () => clearFilters(event))}
        >
          Clear filters
        </div>
      </div>
    </div>
  );
};

export default CurrentFilter;
