/* eslint-disable jsx-a11y/tabindex-no-positive */
import { useState, useEffect, UIEvent } from 'react';
import cn from 'classnames';

import handleButtonKeys from 'src/utils/handleButtonKeys';
import { GlassesColourTypes, ColourFilterSamples, GlassesFrameTypes } from 'src/api/collection';
import { useCollectionContext, updateFilters } from 'src/modules/store/collection';

import styles from './Filters.module.scss';

const Filters = () => {
  const [isColourFilterDisplayed, setColourFilterDisplayed] = useState(false);
  const [isShapeFilterDisplayed, setShapeFilterDisplayed] = useState(false);

  const hideFilterPanel = () => {
    setColourFilterDisplayed(() => false);
    setShapeFilterDisplayed(() => false);
  };

  useEffect(() => {
    if (window) {
      window.addEventListener('mousedown', hideFilterPanel);

      return () => window.removeEventListener('mousedown', hideFilterPanel);
    }
    return undefined;
  }, []);

  const {
    state: { colourFilter, frameFilter },
    dispatch: collectionDispatch
  } = useCollectionContext();

  const toggleColorFilterDisplay = (event: UIEvent) => {
    event.preventDefault();
    event.stopPropagation();

    setColourFilterDisplayed((previousDisplay) => !previousDisplay);
    setShapeFilterDisplayed(() => false);
  };

  const toggleShapeFilterDisplay = (event: UIEvent) => {
    event.preventDefault();
    event.stopPropagation();

    setShapeFilterDisplayed((previousDisplay) => !previousDisplay);
    setColourFilterDisplayed(() => false);
  };

  const toggleColorFilter = (event: UIEvent, colour: GlassesColourTypes) => {
    event.preventDefault();
    event.stopPropagation();

    const newFilter = {
      ...colourFilter
    };
    if (newFilter[colour]) {
      // to do not clutter URL parameters in requests, we delete unused entries
      delete newFilter[colour];
    } else {
      newFilter[colour] = true;
    }
    collectionDispatch && collectionDispatch(updateFilters(newFilter, frameFilter));
  };

  const toggleFrameFilter = (event: UIEvent, frame: GlassesFrameTypes) => {
    event.preventDefault();
    event.stopPropagation();

    const newFilter = {
      ...frameFilter
    };
    if (newFilter[frame]) {
      // to do not clutter URL parameters in requests, we delete unused entries
      delete newFilter[frame];
    } else {
      newFilter[frame] = true;
    }
    collectionDispatch && collectionDispatch(updateFilters(colourFilter, newFilter));
  };

  return (
    <div className={styles.buttons}>
      <div
        className={cn(styles.button, { [styles.buttonActive]: isColourFilterDisplayed })}
        role="button"
        tabIndex={400}
        aria-label="Open colour filters popup"
        aria-haspopup="true"
        aria-expanded={isColourFilterDisplayed}
        onMouseDown={(event) => event.stopPropagation()}
        onClick={toggleColorFilterDisplay}
        onKeyDown={(event) => handleButtonKeys(event, () => toggleColorFilterDisplay(event))}
      >
        Colour
      </div>
      <div
        className={cn(styles.button, { [styles.buttonActive]: isShapeFilterDisplayed })}
        role="button"
        tabIndex={500}
        aria-label="Open shape filters popup"
        aria-haspopup="true"
        aria-expanded={isShapeFilterDisplayed}
        onMouseDown={(event) => event.stopPropagation()}
        onClick={toggleShapeFilterDisplay}
        onKeyDown={(event) => handleButtonKeys(event, () => toggleShapeFilterDisplay(event))}
      >
        Shape
      </div>
      <div
        className={cn(styles.popup, { [styles.popupOpen]: isColourFilterDisplayed })}
        role="search"
        aria-label="Filter by colour"
      >
        {Object.values(GlassesColourTypes).map((colourType) => (
          <div
            key={colourType}
            className={cn(styles.item, { [styles.itemSelected]: colourFilter[colourType] })}
            role="checkbox"
            aria-checked={!!colourFilter[colourType]}
            tabIndex={400}
            onMouseDown={(event) => event.stopPropagation()}
            onClick={(event) => toggleColorFilter(event, colourType)}
            onKeyDown={(event) => handleButtonKeys(event, () => toggleColorFilter(event, colourType))}
          >
            <div className={styles.colourSample}>
              <img
                className={styles.sampleImg}
                src={ColourFilterSamples[colourType]}
                alt={`Sample for ${colourType} filter`}
              />
            </div>
            <span>{colourType}</span>
          </div>
        ))}
      </div>
      <div
        className={cn(styles.popup, styles.popupFrames, { [styles.popupOpen]: isShapeFilterDisplayed })}
        role="search"
        aria-label="Filter by shape"
      >
        {Object.values(GlassesFrameTypes).map((frameType) => (
          <div
            key={frameType}
            className={cn(styles.item, { [styles.itemSelected]: frameFilter[frameType] })}
            role="checkbox"
            aria-checked={!!frameFilter[frameType]}
            tabIndex={500}
            onMouseDown={(event) => event.stopPropagation()}
            onClick={(event) => toggleFrameFilter(event, frameType)}
            onKeyDown={(event) => handleButtonKeys(event, () => toggleFrameFilter(event, frameType))}
          >
            <span>{frameType}</span>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Filters;
