/**
 * Fetching wrappers that are consistent across browsers and APIs
 */
import fetch from 'isomorphic-fetch';
import camelCase from 'lodash/camelCase';

export const API_BASE_URL = 'https://staging-api.bloobloom.com/user/v1/sales_channels/website/';

export type UrlQueryParams = {
  [key: string]: string | number | boolean | Array<string | number | boolean>;
};

type ObjectJsonNode = {
  // eslint-disable-next-line no-use-before-define
  [key: string]: JsonNode;
};
type JsonNode = ObjectJsonNode | Array<JsonNode> | Object | string | number | boolean;

const camelCaseResponse = (obj: JsonNode): JsonNode => {
  if (Array.isArray(obj)) {
    return obj.map((arrVal) => camelCaseResponse(arrVal));
  }
  if (obj != null && obj.constructor === Object) {
    return Object.keys(obj).reduce(
      (result, key) => ({
        ...result,
        [camelCase(key)]: camelCaseResponse((obj as ObjectJsonNode)[key])
      }),
      {}
    );
  }
  return obj;
};

const prepareParams = (url: string, params: UrlQueryParams) => {
  const query = Object.entries(params || ({} as UrlQueryParams))
    .reduce(
      (result: Array<string>, [key, value]) =>
        !value && value !== false
          ? result
          : [
              ...result,
              ...([] as Array<string | number | boolean>).concat(value).map(
                (val) =>
                  `${encodeURIComponent(key)
                    .replace('%5D', ']')
                    .replace('%5B', '[')}=${encodeURIComponent(val)}`
              )
            ],
      []
    )
    .join('&');

  return query ? `${url}?${query}` : url;
};

const validateResponse = (response: Response): Promise<any> => {
  if (response.ok) {
    return response.json();
  }

  if (response.status === 204) {
    return Promise.resolve();
  }

  // TODO: vgrynch: add api errors handling and converting into the exception - depends on API
  if (response.status >= 500) {
    throw new Error(`Server API error, status: ${response.status}`);
  }

  throw new Error(`Unhandled API error, status: ${response.status}`);
};

export const fetchJson = (url: string, params?: UrlQueryParams): Promise<any> => {
  const path = params ? prepareParams(url, params) : url;

  const headers = {
    // TODO: vgrynch: add session token sending here if need to
    'Content-Type': 'application/json',
    Cache: 'no-cache'
  };

  return fetch(path, {
    // credentials: 'include', // if we have a cross-site API URLs, APIs must provide headers to use "include" here
    method: 'GET',
    headers
  })
    .then(validateResponse)
    .then(camelCaseResponse);
};
