import { API_BASE_URL, fetchJson, UrlQueryParams } from './fetch';

const API_URL_BASE = `${API_BASE_URL}collections/`;
const API_URL_REST = '/glasses';

export enum GlassesStatus {
  ACTIVE = 'active'
}

export enum GlassesMediaTypes {
  IMAGE = 'image',
  COLOUR = 'colour'
}

export enum GlassesColourTypes {
  BLACK = 'black',
  TORTOISE = 'tortoise',
  COLOURED = 'coloured',
  CRYSTAL = 'crystal',
  DARK = 'dark',
  BRIGHT = 'bright'
}

export enum GlassesFrameTypes {
  SQUARE = 'square',
  RECTANGLE = 'rectangle',
  ROUND = 'round',
  CAT_EYE = 'cat-eye'
}

export type GlassesColourFilter = {
  [Property in GlassesColourTypes]?: boolean;
};

export type GlassesFrameFilter = {
  [Property in GlassesFrameTypes]?: boolean;
};

export interface GlassesMedia {
  id: number;
  mediumType: GlassesMediaTypes;
  mimeType: string;
  fileLocation: string;
  filename: string;
  originalFileName: string;
  size: number;
  url: string;
  position: number;
}

export interface GlassesColour {
  id: number;
  name: string;
  media: Array<GlassesMedia>;
}

export interface GlassesVariant {
  id: number;
  barcode: string | null;
  harmonizedSystemCode: string | null;
  stockKeepingUnit: string | null;
  inventory: boolean;
  homeTrialAvailable: boolean;
  price: number;
  defaultGlassVariant: boolean;
  frameVariant: {
    id: number;
    name: string;
    configurationName: string;
    barcode: string | null;
    harmonizedSystemCode: string | null;
    stockKeepingUnit: string | null;
    status: GlassesStatus;
    colour: GlassesColour;
  };
  media: Array<GlassesMedia>;
}

export interface GlassesItem {
  id: number;
  name: string;
  configurationName: string;
  defaultCollectionName: string | null;
  costBreakdown: {
    id: number;
    materials: number;
    labour: number;
    transport: number;
    taxes: number;
    bloobloomPrice: number;
    retailPrice: number;
  };
  glassVariants: Array<GlassesVariant>;
}

interface CollectionResponse {
  glasses: Array<GlassesItem>;
  meta: {
    totalCount: number;
  };
}

// TODO: v grynch: below probably should come from API
export const ColourFilterSamples = {
  [GlassesColourTypes.BLACK]: 'https://d32y5z2afvomc1.cloudfront.net/assets/filters_black_01.png',
  [GlassesColourTypes.TORTOISE]: 'https://d32y5z2afvomc1.cloudfront.net/assets/filters_tortoise_02.png',
  [GlassesColourTypes.COLOURED]: 'https://d32y5z2afvomc1.cloudfront.net/assets/filters_coloured_03.png',
  [GlassesColourTypes.CRYSTAL]: 'https://d32y5z2afvomc1.cloudfront.net/assets/filters_crystal_04.png',
  [GlassesColourTypes.DARK]: 'https://d32y5z2afvomc1.cloudfront.net/assets/filters_dark_05.png',
  [GlassesColourTypes.BRIGHT]: 'https://d32y5z2afvomc1.cloudfront.net/assets/filters_bright_06.png'
};

const fetchCollection = (
  collectionId: string,
  itemsPerPage: number,
  page: number,
  colourFilter: GlassesColourFilter,
  frameFilter: GlassesFrameFilter
): Promise<CollectionResponse> => {
  const url = `${API_URL_BASE}${collectionId}${API_URL_REST}`;
  const params: UrlQueryParams = {
    'sort[type]': 'collection_relations_position',
    'sort[order]': 'asc',
    'filters[lens_variant_prescriptions][]': 'fashion',
    'filters[lens_variant_types][]': 'classic',
    'filters[frame_variant_home_trial_available]': false,
    'page[limit]': itemsPerPage,
    'page[number]': page,
    'filters[glass_variant_frame_variant_colour_tag_configuration_names][]': Object.keys(colourFilter),
    'filters[glass_variant_frame_variant_frame_tag_configuration_names][]': Object.keys(frameFilter)
  };
  return fetchJson(url, params);
};

export default fetchCollection;
